import React from "react";
import { Footer } from "react-materialize";

class FooterClass extends React.Component {
	state = {};

	render() {
		return (
			<div className="footerHome">
				<Footer
					copyrights="2018 &copy; Microsoft Student Technical Club. All Rights Reserved"
					moreLinks={
						<div style={{ marginTop: "-20px" }}>
							<a
								className="socialIcon"
								style={{ color: "red" }}
								href="https://www.facebook.com/mstcatdaiict/"
							>
								<i
									className="fa fa-facebook"
									style={{ fontSize: "24px" }}
								/>
							</a>
							<a
								className="socialIcon"
								style={{ color: "red" }}
								href="https://www.instagram.com/mstc.daiict/?hl=en"
							>
								<i
									className="fa fa-instagram"
									style={{
										marginRight: "10px",
										fontSize: "24px"
									}}
								/>
							</a>
						</div>
					}
					className="example black"
				/>
			</div>
		);
	}
}

export default FooterClass;



