import React from 'react';
import { Col,Card } from 'react-materialize';

import ideoson from '../images/ideoson.jpg';

class Ideoson extends React.Component{

    render(){
        return (
            <div>
                <img className="dream" src={ideoson} alt="ideoson" />
                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='Ideoson'>
                        <div className="text">
                            Do you have an idea that u believe will be turning into a ravishing app? Do 
                            you feel that your idea is very compact and will disappear into a puff of smoke 
                            as soon as it enters into the real world out there? Sometimes very small ideas 
                            make a great difference.It’s your idea! Make it large. You never know what lurks 
                            beneath the surface of your fragile sanity.Share it! Share it in a world of app 
                            enthusiasts, lovers and experienced developers, know what difference you can make 
                            and where your idea stands.<br /><br />   

                            Microsoft Club DA-IICT is back with an exciting event, “MS Ideoson” for all the 
                            enthusiasts who have an idea out there. Ideoson is all about developing your idea 
                            into a live app and giving it a new face and scope. There are no prerequisites for 
                            the event, you might be a pro or a novice or even a beginner with no prior app 
                            development experience. All you need to have is an interesting idea upon which you 
                            believe can turn into a million download application.
                        </div>
                    </Card>
                </Col>

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='Phases'>
                        <div className="phases">
                            The event is divided into following phases
                        </div>
                        <ol className="phases1">
                            <li>Propose your idea.</li>
                            <li>Grab votes for your idea.</li>
                            <li>Present your idea.</li>
                            <li>Make a team with our skilled members.</li>
                            <li>Develop Apps during the summer.</li>
                            <li>Launch them in windows store and participate in your dream competitions.</li>
                        </ol>
                    </Card>
                </Col>

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='Judgin Criteria'>
                        <div className="text">
                            The app ideas you suggest will be judged by various field experts with humongous 
                            experience in the industry, the club members and also the student community of DA-IICT 
                            through an extensive voting process. The top ideas will be awarded and will be taken 
                            up by the skilled volunteers of the Microsoft Club who will form a team with you. 
                            Each team formed will be assigned a mentor who will make sure your app sees the light 
                            of the competitions like Microsoft Imagine Cup, Microsoft BizSpark India StartUp
                            Challenge, mBillionth App Competition etc. You will also learn app development in 
                            this course of time. This is a very good opportunity for budding app developers.
                        </div>
                    </Card>
                </Col>
            </div>
        );  
    }
}

export default Ideoson;



