import React from 'react';
import {Collapsible,CollapsibleItem} from 'react-materialize';

class Events extends React.Component{

  componentDidMount(){
        document.title = "Events";
  }

    render(){
        return (
            <div className="eventsCont">
                <Collapsible>
                    <CollapsibleItem header='Cognitive Service Workshop'>
                        <p>
                            Artificial intelligence is a hot topic nowadays. And when we speak of AI we can&#39;t forget to mention
                            cognitive services. MSTC successfully conducted a session on cognitive services using Azure. Our
                            esteemed speaker Mr Jinal Shah who is a certified Microsoft trainee had conducted the session. The
                            cognitive services workshop was one of our most successful events, with a participation of around 200
                            students. They were taught how to use Voice and Face recognition for their applications which will help
                            them to compete with the latest upcoming in this field.
                        </p>
                        <p>  
                            The session was supposed to be of 3 hours but enthusiasm from the participants was on such a peak
                            that it went on for 4 hours. Not only did the students learn a lot, but we also had given a small feast of
                            McDonald&#39;s burger. Participants were also awarded certificates from Microsoft.
                        </p>   
                    </CollapsibleItem>

                    <CollapsibleItem header='Angular With Firebase'>
                        <p>
                            Every student dreams to be able to make an awesome real-time project of his own, but with not much
                            good resources available, students are not able to do it. So MSTC conducted a session on the usage of
                            Angular and Firebase from scratch. They were taught the usage of creating a real-time database and
                            perform CRUD(Create, Run, Update, Delete) operations in Angular and Firebase. The session was
                            conducted by Microsoft trainee Mr Jinal Shah who is also a Microsoft MVP(Most valued professional).
                        </p>
                        <p>
                            The attendees gained a deep knowledge of the inner workings of the cool stuff like using a real-time
                            database, free hosting, easy authentication services and cloud messaging. Not only this, but the
                            students also learnt an in-depth knowledge of Angular- a front-end framework.
                        </p>
                        <p>
                            Even though the session was about 2 hours long, yet people were very excited and willing to stay late to
                            get their doubts cleared. It wasn’t just about learning a new skill but actually about using the
                            imagination and creativity into making something quite extraordinary and satisfactory.
                        </p>
                    </CollapsibleItem>

                    <CollapsibleItem header='Face-Off Coding Competition'>
                        <p>
                            Coding competitions are held so that the students can know where they stand among their peers and
                            can work on their weaknesses. Face-off was one such event which was organized by MSTC where
                            students could test their grasp on coding and compete among the similar minds. The format was quite
                            different from rest of coding competitions. Participants were made to compete in between pairs and the
                            first one to code between them went through the further round. The participation was so huge that we
                            have to conduct 5 rounds each for first year and rest of the batch. As the first year are newbies, so we
                            conducted different rounds for them. This competition was unlike any other held before, for the
                            participants didn&#39;t know what awaited them towards the final round. All those who cleared were made
                            to take sips of non - diluted lemon juice within the given interval with extra lemon juice for the penalty.
                            Fun when mixed with competitions like these bring up great events to participate and conduct. There
                            was also prize money for the winners. It was first time in DA-IICT that such a competition was
                            conducted.
                        </p>
                    </CollapsibleItem>

                    <CollapsibleItem header="Unity Game Development">
                        <p>
                            One of the common dreams of a student in this era is to develop a game of his/her choice that everyone
                            is using and admires. But, without proper support or guidance, this talented bunch of people is left out!
                            MSTC took the lead and successfully conducted Unity Game Development sessions for a one and half
                            month starting from scratch to project-driven sessions. The turnout of the students was really inspiring.
                            The introductory sessions were focused on the need and establishment of facts/figures, sorting out
                            installation problems. We were happy and elated to see such a large participation till the end.
                        </p>
                        <p>
                            The sessions were held weekly and people either came early or stayed late to interact more and acquire
                            more knowledge from the mentors who willingly and tirelessly shared their experiences. It wasn&#39;t just
                            about learning a new skill but rather it was more about the fact of putting creativity into the games and
                            enjoying the process. The sessions continued with the Unity Platform and workspace explanation,
                            animation, colour coding but 3D was the show stealer! The sessions were supposed to be between 2
                            and a half hours in the evening but easily went to more than 3 hours. All the participants were given
                            certificates on completing this series of events.
                        </p>
                    </CollapsibleItem>

                    <CollapsibleItem header="Python">
                        <p>
                            Python is one of the most important language in today’s world and it’s a must learn language for
                            students of computer science. With its various applications and uses, it has become a must know
                            requirement for a good developer. So we at MSTC believe in making DA-IICT students industry ready and
                            for the same, we had conducted a session on python. In the session, students were first given a good
                            understanding of the basics of python, so that students can learn advanced stuff easily. Subsequently,
                            they were taught Web Scraping in which they learnt how to extract data from websites and how to use
                            it for their own stuff. It was a very fruitful and dynamic session as students were very interactive and
                            enthusiastic to clear their concepts and doubts. Students were also given goodies at the end of the
                            session.
                        </p>
                    </CollapsibleItem>

                    <CollapsibleItem header="AECC Global Seminar">
                        <p>
                            Apart from teaching technologies, MSTC also conducted a session on what to pursue further after
                            graduation as many students are very confused on what to do and which path to follow. So to help the
                            students, MSTC in collaboration with AECC Global conducted a session on “Higher Studies in USA,
                            Canada, Australia” so that they can know the procedure and requirements of the same. The session was
                            conducted by qualified trainers from AECC Global who were having experience of more than 10 years in
                            this field.
                        </p>
                    </CollapsibleItem>
                </Collapsible>
            </div>
        );  
    }
}

export default Events;



