import React from 'react';
import { Navbar } from 'react-materialize';
import {Link} from 'react-router-dom';
// ,Dropdown,Button,NavItem,NavLink
import logo from '../images/logo.jpg';

class Header extends React.Component{

    render(){
        return (
            <div className="navbarHome">
                <Navbar brand='' left className="navBar white">
                    <div className="dropdown12">
                        <Link className="link" to="/">HOME</Link>
                    </div>
                    <div class="dropdown12">
                        <span style={{color:"black"}}>EVENTS</span>
                        <div class="dropdown-content12" style={{color:'pink'}}>
                            <Link to="/upcoming">UPCOMING</Link>
                            <Link to="/events">PAST</Link>
                        </div>
                    </div>
                    <div className="dropdown12">
                        <Link className="link" to="/dreamspark">DREAMSPARK</Link>
                    </div>
                    <div className="dropdown12">
                        <Link className="link" to="/gallery">GALLERY</Link>
                    </div>
                    <div className="dropdown12">
                        <Link className="link" to="/aboutus">ABOUT US</Link>
                    </div>
                    <div className="dropdown12">
                    <Link className="link" to="/contact">CONTACT US</Link>
                    </div>
                </Navbar>
                <img className="brandLogo" src={logo} alt="logo" />
            </div>
        );
    }
}

export default Header;



