import React from 'react';
import { Carousel,Card,Col } from 'react-materialize';

import first from '../images/car/1.JPG';
import second from '../images/car/2.JPG';
import third from '../images/faceoff/2.png';
import fourth from '../images/cog/5.JPG';

class Home extends React.Component{

        componentDidMount(){
            document.title = "MSTC";
            console.log(this.props.history);
        }
    render(){

        return (
            <div>
                <div className="carCont">
                    <Carousel images={[
                      first,
                      second,
                      third,
                      fourth
                    ]} />
                </div>

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='About MSTC'>
                        <div className="text">
                        We are individuals who come together and share common interest putting our ideas 
                        into existence in order to contribute to the Microsoft Community. Yes, we are the
                        <strong className="mstc"> MSTC-Microsoft Student Technical Club</strong> of 
                        <a className="dalink" href="http://www.daiict.ac.in/" target="_blank" rel="noopener noreferrer"> DAIICT</a>.
                            We try to make ‘I don’t know’ to ‘I don’t know yet’.<br />
                            Many people have genuine ideas related to app and web development but there is
                            no proper platform provided to them. We, as MSTC, come here and help them give 
                            the right environment to inculcate their ideas and develop for Microsoft. We aim 
                            to do this in as exciting and simplified manner as possible. If you want to find 
                            answers to your questions, share ideas, solve problems, or whatever may be your 
                            goal, MSTC is here to help you get more out of your ideas and technology. 
                            If you need help browse our club, or post a question and our community members and 
                            volunteers can help you understand the problem with advice or step by-step 
                            instructions.<br />
                            We also come up with various events and competitions which would help you to create 
                            a strong foundation for your dream competitions like those of Imagine Cup. We are 
                            here to make you experience the windows development environment and encourage you 
                            to develop and contribute towards the same.
                        </div>
                    </Card>
                </Col>
            </div>
        );  
    }
}

export default Home;



