import React from 'react';
import { Col,Card } from 'react-materialize';

import imagine from '../images/imagine.jpg';

class Dreamspark extends React.Component{

    componentDidMount(){
        document.title = "Dreamspark";
    }

    render(){
        return (
            <div>
                <img className="dream" src={imagine} alt="dreamspark" />
                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='What is Microsoft Imagine'>
                        <div className="text">
                            Microsoft Imagine (previously known as Dreamspark) connects students with the tools, 
                            resources and experiences they need to elevate their skills for<br />
							today’s working world. Whether it’s building a game, designing an app or launching a 
							project, Microsoft Imagine helps
							students develop their ideas and boldly bring them to life.<br />

							DA-IICT has a Premium license to ensure that we get all the essential software and DO NOT USE PIRATED or CRACKED
							versions. Our college has invested significant amount to support us, so please make the most of the available resources
							and enjoy the benefits of being a student at DA-IICT.
                        </div>
                    </Card>
                </Col>

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='Commercial Products Available'>
                        
                        <ol className="product">
                            <li>Windows 7 , 8 , 8.1 , 10.</li>
                            <li>Visual Studio 2008, 2010, 2012, 2013 and 2015 Professional Editions.</li>
                            <li>Visual Studio Light Switch 2011.</li>
                            <li>Expression Studio 4 Ultimate.</li>
                            <li>Microsoft SQL Server 2008,2012,2014.</li>
                            <li>Windows Server 2008,2012 Standard Edition 32-bit, 64-bit.</li>
                        </ol>
                    </Card>
                </Col>

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='Procedure To Apply'>
                        
                        <ol className="product">
                            <li><b>Only DAIICT student can apply for Imagine account.</b></li>
                            <li><b>Once in every academic year,Webmail will be sent regarding it.</b></li>
                            <li>You need to fill the form which will be sent along with the mail during the activation period.</li>
                            <li>It will take approximately 2 weeks to handle DreamSpark request.</li>
                            <li>DAIICT Imagine webstore&nbsp;Login Link :
                            	<a className="dalink" href="https://e5.onthehub.com/WebStore/ProductsByMajorVersionList.aspx?ws=952c61e5-2ecd-e211-9d05-f04da23e67f4&amp;vsro=8">Click here</a></li>
                        </ol>
                    </Card>
                </Col>
            </div>
        );  
    }
}

export default Dreamspark;



