import React, { Component }  from 'react';
import ReactDOM from 'react-dom';
import registerServiceWorker from './registerServiceWorker';
import { Route,HashRouter } from 'react-router-dom';

import './index.css';
import Header from './components/Header';
import Gallery from './components/Gallery';
import Home from './components/Home';
import Hackathon from './components/Hackathon';
import Dreamspark from './components/Dreamspark';
import Ideoson from './components/Ideoson';
import Events from './components/Events';
import AboutUs from './components/AboutUs';
import Contact from './components/Contact';
import FooterClass from './components/Footer';
import Upcoming from './components/Upcoming';

class App extends Component {
  render() {
    return (
    	<HashRouter>
			<div className="App">
	        	<Header />   	
		            <Route exact path='/' component={Home} />
		            <Route exact path='/contact'  component={Contact} />
		            <Route exact path='/gallery'  component={Gallery}  />
		            <Route exact path='/upcoming' component={Upcoming } />
		            <Route exact path='/aboutus' component={AboutUs} />
		            <Route exact path='/hackathon' component={Hackathon} />
		            <Route exact path='/dreamspark' component={Dreamspark} />
		            <Route exact path='/events' component={Events} />
		            <Route exact path='/ideoson' component={Ideoson} />
		        
		        <FooterClass />
	      	</div>
      	</HashRouter>
    );
  }
}

ReactDOM.render(<App />, document.getElementById('root'));
registerServiceWorker();



