import React from 'react';
import { Col,Card,Table,CardTitle,Row } from 'react-materialize';

import hackathon from '../images/hackathon.jpg';
import sap from '../images/sap.jpg';
import dayzerts from '../images/dayzerts.jpg';
import iic from '../images/iic.png';
import satkar from '../images/satkar.jpg';

class Hackathon extends React.Component{

    render(){
        return (
            <div>
                <img className="hackimg responsive-img" src={hackathon} alt="hackathon" />

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='About Hackathon'>
                        <div className="text">
                            Microsoft Club DAIICT presents Hackathon ’17. It is an initiative taken by the club to 
                            bring out creativity in young minds and transform it into a working application. If you have 
                            a penchant for developing innovative apps, this is the perfect opportunity for you to show 
                            off your skills. Participants can develop an app on any platform be it a web app or a mobile 
                            app. Form groups of maximum five, develop an app in next 36 hours on a given theme and win 
                            loads of prizes. A panel of experts in this field will judge your work. Don’t miss this 
                            opportunity and register as soon as possible, as we have limited number of seats. During 
                            Hackathon , we would provide food and other basic stuff like wifi, charging ports etc. 
                            Bring your resources like laptops with you.
                        </div>
                    </Card>
                </Col>

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='Registration Fees'>
                        <div className="text">
                            Registration Fees is Rupees 100/- per person
                        </div>
                        <strong className="fees">LAST DATE OF REGISTRATION IS 6TH JAN 2017  TILL 02:00 PM</strong>
                    </Card>
                </Col>

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='Phases'>
                        <div className="phases">
                            The event is divided into following phases
                        </div>
                        <ol className="phases1">
                            <li>Registration and announcement of the theme.</li>
                            <li>Built a working app on platform you wish in 36 hours.</li>
                            <li>At the end, give a small presentation in front of experts.</li>
                            <li>Winners will be awarded with prizes.</li>
                        </ol>
                    </Card>
                </Col>

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='Rules'>
                        <ol className="phases1">
                            <li>There can be minimum 1 and maximum 5 members in a team.</li>
                            <li>To ensure a level field for all contestants, all code, design, etc. assets must be created/developed during the Hackathon only, so that all the participants start off on the same footing.</li>
                            <li>Internet and Wifi connectivity will be provided throughout the event.</li>
                            <li>To participate in the Hackathon, each member of the team, must attend the Hackathon event and be registered for the Hackathon. Participants may register for the Hackathon online through the website.</li>
                            <li>The Team should identify a “team leader” at the time of registration itself.</li>
                            <li>App development can be done on mobile and web platforms.</li>
                            <li>Participants should bring their own resources(Laptops,chargers etc.) .</li>
                            <li>It is mandatory for the participants to bring their personal identity proof.</li>
                            <li>Theme for App development will be provided on the time of the Hackathon&nbsp;itself only.</li>
                            <li>Any member of the team can take break for refreshments at any time.</li>
                            <li>In case of any dispute, the decision taken by event heads will be final.</li>
                            <li>If any participant is found to be cheating, the whole team will be disqualified.</li>
                            <li>Coding should be done in the specified time only. No Extra time shall be provided.</li>
                            <li>There should not be any virtual member.(i.e. A member who is not part of the team may not take part virtually by sending mails or helping the team in a manner whatsoever.)Any team found involved in such activity would straight away be disqualified.</li>
                            <li>No type of PLAGIARISM would be entertained.Any team found doing so will be disqualified straight away.</li>
                        </ol>
                    </Card>
                </Col>

                <Col className="small">
                    <Card className='white darken-3 title' textClassName='black-text' title='Schedule'>
                        <Table>
                            <thead>
                              <tr>
                                  <th>6th January 2017</th>
                                  <th>7th January 2017</th>
                                  <th>8th January 2017</th>
                              </tr>
                            </thead>

                            <tbody>
                              <tr>
                                <td>7:00 PM - Registration</td>
                                <td>8:00 AM - Breakfast</td>
                                <td>8:00 AM - Breakfast</td>
                              </tr>
                              <tr>
                                <td>7:30 PM - Welcome address and theme revelation</td>
                                <td>11:00 PM –  Mentoring session</td>
                                <td>10:00 AM  –  Hackathon Ends</td>
                              </tr>
                              <tr>
                                <td>08:00 PM – Dinner</td>
                                <td>12:00 PM –  Lunch</td>
                                <td>11:00 AM  –  Expo  </td>
                              </tr>
                              <tr>
                                <td>12:00 AM  –  Midnight Snacks</td>
                                <td>05:00  PM –  Snacks</td>
                                <td>12:00 PM  –  Lunch</td>
                              </tr>
                              <tr>
                                <td></td>
                                <td>09:00 PM – Dinner</td>
                                <td>01:00 PM – Presentation to Jury</td>
                              </tr>
                              <tr>
                                <td></td>
                                <td></td>
                                <td>03:30 PM – Tea Break</td>
                              </tr>
                              <tr>
                                <td></td>
                                <td></td>
                                <td>04:00 PM – Presentation Continue</td>
                              </tr>
                              <tr>
                                <td></td>
                                <td></td>
                                <td>05:30 PM – Awards and closing ceremony</td>
                              </tr>
                            </tbody>
                          </Table>
                    </Card>
                </Col>
                <Row className="activities">
                    <h4>Sponsers</h4>
                    <Col className="activity">
                        <Card className="event"
                            header={<CardTitle image={satkar}></CardTitle>}>
                        </Card>
                    </Col>
                    <Col className="activity">
                        <Card className="event"
                            header={<CardTitle image={iic}></CardTitle>}>
                        </Card>
                    </Col>
                    <Col className="activity">
                        <Card className="event"
                            header={<CardTitle image={sap}></CardTitle>}>
                        </Card>
                    </Col>
                    <Col className="activity">
                        <Card className="event"
                            header={<CardTitle image={dayzerts}></CardTitle>}>
                        </Card>
                    </Col>
                </Row>
            </div>
        );  
    }
}

export default Hackathon;



