import React from 'react';
import ImageZoom from 'react-medium-image-zoom'

import first from '../images/aecc/AECC Seminar -1.jpeg';
import second from '../images/aecc/AECC Seminar -2.jpeg';
import third from '../images/aecc/AECC Seminar -3.jpeg';
import fourth from '../images/aecc/AECC Seminar -4.jpeg';

import cfirst from '../images/cog/1.JPG';
import csecond from '../images/cog/2.JPG';
import cthird from '../images/cog/3.JPG';
import cfourth from '../images/cog/4.JPG';
import cfifth from '../images/cog/5.JPG';

import ffirst from '../images/faceoff/1.png';
import fsecond from '../images/faceoff/2.png';
import fthird from '../images/faceoff/3.png';
import ffourth from '../images/faceoff/4.png';

import pfirst from '../images/python/1.jpg';
